import React, { useState } from 'react';
import { Paper, TableContainer } from '@mui/material';
// import InventoryListItem from './';
import MachineData from '../../../utilities/dummy_data/machine_list.json';
import CustomPagination from '../Pagination';
import  EquipmentListHeader  from './EquipmentsListHeader';
import EquipmentListItem from './EquipmentListItem';

const EquipmentTable = () => {
  const [currentPage, setCurrentPage] = useState(1);
  const productsPerPage = 6;

  // Calculate total number of pages
  const totalItems = MachineData.length;
  const totalPages = Math.ceil(totalItems / productsPerPage);

  // Get current products
  const indexOfLastProduct = currentPage * productsPerPage;
  const indexOfFirstProduct = indexOfLastProduct - productsPerPage;
  const currentProducts = MachineData.slice(indexOfFirstProduct, indexOfLastProduct);

  // Change page
  const handlePageChange = (event, value) => {
    setCurrentPage(value);
  };

  // TODO: Change to MUI Data Table
  return (
    <TableContainer
      component={Paper}
      elevation={0}
      sx={{
        border: '0.5px solid rgba(181, 181, 181, 1)',
        background: 'linear-gradient(0deg, #FFFFFF, #FFFFFF), linear-gradient(0deg, #B5B5B5, #B5B5B5)',
        boxShadow: 'none',
        ml: '1rem',
        width: 'auto',
        borderRadius: '8px',
        mr: '1rem'
      }}
    >
      <EquipmentListHeader />
      {currentProducts.map((product) => (
        <EquipmentListItem key={product.id} product={product} />
      ))}
      <CustomPagination count={totalPages} page={currentPage} onChange={handlePageChange} />
    </TableContainer>
  );
};

export default EquipmentTable;
