import React from 'react';
import { Formik, Field } from 'formik';
import {
  TextField,
  Button,
  MenuItem,
  Grid,
  Box,
  Typography,
  Link,
  Checkbox,
  FormControl,
  FormControlLabel,
  FormGroup,
  FormLabel
} from '@mui/material';
import * as Yup from 'yup';
import { Link as RouterLink } from 'react-router-dom';
import FormField from '../shared/FormField';

// Yup validation schema
const validationSchema = Yup.object().shape({
  name: Yup.string().required('Name is required'),
  email: Yup.string().email('Invalid email').required('Email is required'),
  phoneNumber: Yup.string().required('Phone number is required'),
  position: Yup.array().min(1, 'At least one position must be selected'),
});

const RegistrationForm = () => {
  return (
    <Formik
      initialValues={{
        name: '',
        email: '',
        phoneNumber: '',
        position: [],
      }}
      validationSchema={validationSchema}
      onSubmit={(values) => {
        console.log(values);
        // Submit form values
      }}
    >
      {({ handleSubmit, values, handleChange, handleBlur, touched, errors, resetForm }) => (
        <Box
        sx={{
          maxWidth: '75rem', // Adjust the width as necessary
          mx: 'auto', // Centers the box
          my: 4, // Margin top and bottom
          p: 2, // Padding inside the box
          boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.1)', // Example shadow
          borderRadius: '16px', // Adjust as necessary
          bgcolor: 'background.paper', // Or another color as per your design
        }}
      >
        <form onSubmit={handleSubmit}>
          <Grid container spacing={8}>
            <Grid item xs={12} sm={6}>
              {/* Email and Name fields */}
              <FormField
                name="email"
                label="Enter your email"
                fieldType="textfield"
                heading="Email Address"
              />
              <FormField
                name="name"
                label="Enter your name"
                fieldType="textfield"
                heading="Name"
              />
              <FormField
                name="phoneNumber"
                label="Enter phone no."
                fieldType="textfield"
                heading="Phone No."
              />
              {/* Student toggle switch would go here */}
            </Grid>
            <Grid item xs={12} sm={6}>
              {/* Position checkboxes */}
              <FormControl component="fieldset">
                <FormLabel component="legend" variant="h6" sx={{ fontWeight: 'high', marginBottom: 1, color : 'grey'}}>Position</FormLabel>
                <FormGroup>
                <FormControlLabel
                      control={
                        <Checkbox
                          id="position1"
                          name="position"
                          value="TL student management body"
                          checked={values.position.includes("TL student management body")}
                          onChange={handleChange}
                        />
                      }
                      label="TL student management body"
                    />
                    <FormControlLabel
                      control={
                        <Checkbox
                          id="position2"
                          name="position"
                          value="Manager (other technical clubs)"
                          checked={values.position.includes("Manager (other technical clubs)")}
                          onChange={handleChange}
                        />
                      }
                      label="Manager (other technical clubs)"
                    />
                    <FormControlLabel
                      control={
                        <Checkbox
                          id="position3"
                          name="position"
                          value="TL Manager"
                          checked={values.position.includes("TL Manager")}
                          onChange={handleChange}
                        />
                      }
                      label="TL Manager"
                    />
                    <FormControlLabel
                      control={
                        <Checkbox
                          id="position4"
                          name="position"
                          value="TL Technician"
                          checked={values.position.includes("TL Technician")}
                          onChange={handleChange}
                        />
                      }
                      label="TL Technician"
                    />
                    <FormControlLabel
                      control={
                        <Checkbox
                          id="position5"
                          name="position"
                          value="Other"
                          checked={values.position.includes("Other")}
                          onChange={handleChange}
                        />
                      }
                      label="Other"
                    />
                </FormGroup>
              </FormControl>
              <FormField
                name="position"
                label="Enter your position"
                fieldType="textfield"
                heading=""
              />
            </Grid>
          </Grid>
          <Box sx={{ display: 'flex', justifyContent: 'space-between', mt: 2 }}>
              <Button
                variant="outlined"
                onClick={() => resetForm()}
                sx={{
                  flexGrow: 1, // Button will grow to fill the space
                  mr: 1, // Add margin right to separate it from the other button
                }}
              >
                Cancel
              </Button>
              <Button
                type="submit"
                variant="contained"
                color="primary"
                sx={{
                  flexGrow: 1, // Button will grow to fill the space
                  ml: 8, // Add margin left to separate it from the other button
                }}
              >
                Register
              </Button>
          </Box>
            <Typography variant="body1" sx={{ textAlign: 'right', mt: 2 , mr : 3 }}>
              Already have an account?{' '}
              <Link component={RouterLink} to="/in" underline="hover">
                Login here
              </Link>
            </Typography>
        </form>
      </Box>
      
      )}
    </Formik>
  );
};

export default RegistrationForm;

