import React from 'react';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import Divider from '@mui/material/Divider';
import DashboardIcon from '@mui/icons-material/Dashboard';
import InventoryIcon from '@mui/icons-material/Inventory2';
import ReportIcon from '@mui/icons-material/Assessment';
import SettingsIcon from '@mui/icons-material/Settings';
import LogoutIcon from '@mui/icons-material/Logout';
import { Group } from '@mui/icons-material';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
// No need to import @mui/styles

// Replace this with the logo from your assets
import YourLogo from '../../utilities/dummy_assets/MBF_Logo.png';
import { useNavigate } from 'react-router-dom';

const MenuComponent = () => {
  const navigate = useNavigate();
  const menuItems = [
    { text: 'Dashboard', path: '/dashboard', icon: <DashboardIcon /> },
    { text: 'User Groups', path: '/usergeneral', icon: <Group /> },
    { text: 'Inventory', path: '/inventory', icon: <InventoryIcon /> },
    { text: 'Reports', path: '/reports', icon: <ReportIcon /> },
    { text: 'Settings', path: '/notifications', icon: <SettingsIcon /> },
    { text: 'Logout', path: '/logout', icon: <LogoutIcon /> },
  ];

  const handleListItemClick = (path) => {
    // If you're using React Router
    navigate(path);
  };
  return (
    <Box sx={{
      width: '250px',
      height: '100vh',
      backgroundColor: '#212121',
      color: 'white',
      '.MuiListItemIcon-root': { // This targets all ListItemIcon components inside the Box
        color: 'white', // Set the color for icons
      },
      // Add other styles as needed
    }}>
      <Box sx={{ padding: '10px'}}>
        <img src={YourLogo} alt="Maker Bhavan Foundation" style={{ maxWidth: '100%', maxHeight: '60px' , margin : '30px'}} />
      </Box>
      {/* <Toolbar /> */}
      <Divider />
      <List>
      {menuItems.map((item) => (
        // TODO: Remove depreciated styles
        <ListItem 
          button 
          key={item.text}
          onClick={() => handleListItemClick(item.path)} // Update this line to handle click
          sx={{ backgroundColor: '#212121', margin: 'auto', '&:hover': { backgroundColor: 'white', borderRadius : '10px' , color : "black"} }} // Add styling
        >
          <ListItemText primary={item.text} />
          <ListItemIcon>{item.icon}</ListItemIcon>
          
        </ListItem>
      ))}
    </List>
    </Box>
  );
};

export default MenuComponent;
