import React from 'react';
import { useField } from 'formik';
import { TextField, Typography, Grid, MenuItem } from '@mui/material';

const FormField = ({ name, label, fieldType, options , heading , sx}) => {
  // Use Formik's useField to get field props
  const [field, meta] = useField(name);
  
  // Determine the field to render based on fieldType
  const renderField = () => {
    switch (fieldType) {
      case 'dropdown':
        return (
          <TextField sx={sx}{...field} select fullWidth label={label} error={meta.touched && !!meta.error} helperText={meta.touched && meta.error}>
            {options.map((option) => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>
        );
      case 'textfield':
      default:
        return (
          <TextField 
          sx={sx}
          {...field} 
          fullWidth 
          label={label} 
          error={meta.touched && !!meta.error} 
          helperText={meta.touched && meta.error}
        />
        
        );
    }
  };

  return (
    <>
      <Typography variant="h6" sx={{ fontWeight: 'high', marginBottom: 1, color : 'grey'}}>
        {heading}
      </Typography>
      {renderField()}
    </>

  );
};

export default FormField;
