import React from 'react';
import { List, ListItem, ListItemText, ListItemSecondaryAction, Switch, Typography, Box } from '@mui/material';

const PrivacyContainer = () => {
  const [state, setState] = React.useState({
    desktopPushNotification: true,
    mobilePushNotification: false,
    emailNotification: false,
    SoundNotification: true,
    request1: true,
    request2: false,
    // Add more request states as needed
  });

  const handleToggle = (event) => {
    setState({ ...state, [event.target.name]: event.target.checked });
  };


  const labels = {
    desktopPushNotification: "Location",
    mobilePushNotification: "Discoverability",
    emailNotification: "Profile Avatar visibility",
    SoundNotification : "Sound"
    // ... other keys
  };

  return (
    <Box sx={{ bgcolor: 'background.paper', p: 3 , border:'1px #B5B5B5 solid ', borderRadius : '12px' , marginTop: '1rem', gap : "3rem"}}>
      <List>
        {Object.keys(state).map((key) => (
          <ListItem key={key}>
          <ListItemText 
          sx={{color:'##3F3F3F'}}
            primary={labels[key]}
            secondary="Receive push notification on requests and mentions on your desktop"
            primaryTypographyProps={{
                sx: {
                  fontFamily: 'Roboto',
                  fontSize: '1.5rem', // 24px in rem
                  fontWeight: '400',
                  lineHeight: '1.75rem', // 28px in rem
                  letterSpacing: '0em',
                  textAlign: 'left',
                  color:'##3F3F3F'
                }
              }}
          />
          <ListItemSecondaryAction>
            <Switch
              edge="end"
              onChange={handleToggle}
              checked={state[key]}
              name={key}
            />
          </ListItemSecondaryAction>
        </ListItem>
        
        ))}
      </List>
    </Box>
  );
};

export default PrivacyContainer;
